import { Component, Input, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { BackendService } from '../services/backend.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-header-admin',
  templateUrl: './header.admin.component.html',
  styleUrls: ['./header.admin.component.css']
})
export class HeaderAdminComponent implements OnInit {
  @Input() imageUrl: string;
  @Input() pageTitle: string;
  @Input() helpType: string;
  emailSent = false;
  selectedValue;
  formShowing = false;
  configData;
  userRole = 'guest';
  msgCount$;

  error: any;
  dataLoading: boolean = false;
  brokenNetwork = false;

  constructor(private backendService: BackendService, private router: Router) {
  }

  ngOnInit() {
    this.configData = this.backendService.getConfig();
    if(localStorage.getItem("role")) {
      this.userRole = localStorage.getItem("role");
      //this.getMsgCounts();
    }
  }

  getMsgCounts(){
    this.msgCount$ = this.backendService.getUserStudentMSGCounts();
  }
  onSubmit(formData) {
    this.dataLoading = true;
    //console.log(formData);
    this.backendService.sendEmail(formData).subscribe(
      res => {
        //console.log(res);
      },
      error => {
        //console.log(error);
        console.log("API didn't respond.");
        this.brokenNetwork = true;
        this.dataLoading = false;
      },
      () => {
        this.dataLoading = false;
        this.emailSent = true;
      }
    )
  }
  logout() {
    this.backendService.logout()
      .then(
        (success) => {
          this.router.navigate(['/login']);
        }).catch(function (error) {
          console.log(error);
        })
  }

}
