import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { moveIn, fallIn } from '../shared/router.animation';
import { Observable } from 'rxjs';
import { DataSource } from '@angular/cdk/collections';
// import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { BackendService } from '../services/backend.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { UserInfo} from '../model/userinfo'
//import { FormControl, FormGroup, FormBuilder, Validators  } from "@angular/forms";
@Component({
    selector: 'app-student',
    templateUrl: './student.component.html',
    styleUrls: ['./student.component.css'],
    animations: [moveIn(), fallIn()],
    host: { '[@moveIn]': '' }
})
export class StudentComponent implements OnInit, OnDestroy {
    //studentRegistarionForm: FormGroup;
    members: any[];
    dataSource: MatTableDataSource<any>;
    myDocData;
    data$;
    toggleField: string;
    state: string = '';
    savedChanges = false;
    successMessage: String;
    error: boolean = false;
    errorMessage: String = "";
    dataLoading: boolean = false;
    private querySubscription;
    userinfo: UserInfo;

    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['STUDENT_CODE', 'FIRST_NAME', 'FATHER_NAME','_id'];
    feeCDs$;
    marksCDs$;
    enrollmentCDs$;
    // file upload
    docId: string;
    fileName: string;
    showFileUpload: boolean = false;
    showDocument: boolean = false;
    docUrl: Observable<string | null>;
    classes: Array<String>;
    sections: Array<String>;
    studentCode:string;
    genders: Array<String>;
    studentCount: Number;
    constructor(private backendService: BackendService) { }

    ngOnInit() {
        this.toggleField = "searchMode";
        this.dataSource = new MatTableDataSource(this.members);
        //this.initializeForm();
        this.getEnrollmentCDs();
        this.fetchClasses();
        this.fetchSections();
        this.fetchGenders();
    }

    toggle(filter?) {
        if (!filter) { filter = "searchMode" }
        else { filter = filter; }
        this.toggleField = filter;
        this.dataLoading = false;
    }

    getEnrollmentCDs() {
        this.dataLoading = true;
        this.querySubscription = this.backendService.getDocs('ENROLL_CD').subscribe((res) => {
            this.enrollmentCDs$ = res;
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
        },
        () => {
            this.dataLoading = false;
        });
    }

    fetchClasses(){
        this.backendService.fetchDetails('Classes').subscribe(
          {
            next: details =>{
              this.classes=details;
              console.log(this.classes);
            }
          }
        );
    }
    
    fetchSections(){
        this.backendService.fetchDetails('Sections').subscribe(
          {
            next: details =>{
              this.sections=details;
              console.log(this.sections);
            }
          }
        );
    }

    fetchGenders(){
        this.backendService.fetchDetails('Genders').subscribe(
          {
            next: details =>{
              this.genders=details;
              console.log(this.genders);
            }
          }
        );
    }
    

    setStudentData(formData) {
        this.dataLoading = true;
        //Genereate the Student Code
        this.getStudentCount(formData);
    }

    getStudentCount(formData) {
        this.dataLoading = true;
        this.backendService.getDocs('STUDENT').subscribe((res) => {
            this.studentCount =(res !=null) ? (res.length+1) : 1;
            console.log('this.studentCount---->'+this.studentCount);
            if(this.studentCount < 10){
                this.studentCode = 'STD000'+this.studentCount;
            }
            else if(this.studentCount < 100){
                this.studentCode = 'STD00'+this.studentCount;
            }
            else if(this.studentCount < 1000){
                this.studentCode = 'STD0'+this.studentCount;
            }
            else {
                this.studentCode = 'STD'+this.studentCount;
            }
            console.log('this.studentCount---->'+this.studentCode);
            formData.STUDENT_CODE = this.studentCode;
            formData.EMAIL = this.studentCode+'@ig.com';
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
            this.savedChanges = false;
        },
        () => {
            this.dataLoading = false;
        });
        this.saveStudentData(formData);
    }

    saveStudentData(formData) {
        console.log('***********Inside saveStudentData*****');
        this.backendService.setDoc('STUDENT',formData).then(
            (success) => {
                this.createuser(formData)
            },
            (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
            this.savedChanges = false;
            }
        );
    }

    createuser(formData) {
        this.backendService.createUser(formData.EMAIL,'Test1234').then(
          (success) => {
            this.updateUserInfo(formData)
          },
          (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
            this.savedChanges = false;
          }
        );
    }
    
    updateUserInfo(formData) {
        this.userinfo = new UserInfo();
        this.userinfo.email = formData.EMAIL;
        this.userinfo.name =formData.FIRST_NAME +' '+formData.MIDDLE_NAME+' '+formData.LAST_NAME;
        this.userinfo.phone =formData.MOBILE;
        this.userinfo.role='Student';
        this.userinfo.secretKey='12345';
        this.userinfo.activated='Y';
        this.backendService.setDoc('USERS',this.userinfo).then(
            (success) => {
                this.savedChanges = true;
                this.successMessage ='Student Registered Successfull with Resgistration Code '+formData.STUDENT_CODE;
                this.error = false;
                this.errorMessage = "";
                this.dataLoading = false;
            },
            (error) => {
              this.error = true;
              this.errorMessage = error.message;
              this.dataLoading = false;
              this.savedChanges = false;
            }
        );
    }

    getData(formData?) {
        this.dataLoading = true;
        this.querySubscription = this.backendService.getDatalist('STUDENT','Student',formData).subscribe((res) => {
            this.dataSource = new MatTableDataSource(res);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
        },
        () => {
            this.dataLoading = false;
        });
    }

    updateData(formData) {
        this.dataLoading = true;
        this.querySubscription = this.backendService.updateDoc('STUDENT',formData._id,formData).then(res => {
            if (res) {
                this.savedChanges = true;
                this.error = false;
                this.errorMessage = "";
                this.dataLoading = false;
            }
        }
        ).catch(err => {
            if (err) {
                this.error = true;
                this.errorMessage = err.message;
                this.dataLoading = false;
            }
        }
        );
    }

    getDoc(docId) {
        this.docId = docId; // this is required to pass at file upload directive
        this.dataLoading = true;
        this.data$ = this.backendService.getDoc('STUDENT',docId);
        this.toggle('editMode');
        this.dataLoading = false;
    }
    getDocUrl(docUrl){
        this.fileName = docUrl;
        this.docUrl = this.backendService.getFileDownloadUrl(docUrl);
    }

    deleteDoc(docId) {
        if (confirm("Are you sure want to delete this record ?")) {
            this.dataLoading = true;
            this.backendService.deleteDoc('STUDENT',docId).then(res => {
                if (res) {
                    this.error = false;
                    this.errorMessage = "";
                    this.dataLoading = false;
                }
            }
            ).catch(err => {
                if (err) {
                    this.error = true;
                    this.errorMessage = err.message;
                    this.dataLoading = false;
                }
            }
            );
        }
    }

    //mat table paginator and filter functions
    ngAfterViewInit() {
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
    }

    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }
    ngOnDestroy() {
        // this is not needed when observable is used, in this case, we are registering user on subscription
        if (this.querySubscription) {
            this.querySubscription.unsubscribe();
        }
    }

}