import { Component, OnInit, AfterViewInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { environment } from '../../environments/environment';
import { BackendService } from '../services/backend.service';
import { Observable, of } from 'rxjs';
import { UserInfo } from '../model/userinfo'
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit, AfterViewInit {
  socialAuth: boolean = false; // show Google and FB Sign in only when social auth is enabled
  error: any;
  dataLoading: boolean = false;
  brokenNetwork = false;
  data: Observable<any>;
  userInfo: any;
  
  constructor(public afAuth: AngularFireAuth, private backendService: BackendService, private router: Router) { }

  ngOnInit() {
    this.socialAuth = environment.socialAuthEnabled;
  }
  ngAfterViewInit() {
    if (!localStorage.getItem("uid")) {
      this.afAuth.user.subscribe(res => {
        if (res) {
          localStorage.setItem("uid", res.uid);
          localStorage.setItem("displayName", res.displayName);
          localStorage.setItem("email", res.email);
          localStorage.setItem("picture", res.photoURL);
        }
      });
    }
  }
  login(loginType, formData?) {
    this.dataLoading = true;
    this.backendService.login(loginType, formData);
    this.dataLoading = false;
   }
 
  logout() {
    this.backendService.logout()
     .then(
       (success) => {
     //  this._router.navigate(['/login']);
     window.localStorage.removeItem("uid");
     window.localStorage.removeItem("displayName");
     window.localStorage.removeItem("email");
     window.localStorage.removeItem("picture");
     window.localStorage.removeItem("role");
     }).catch(function (error) {
         console.log(error);
       })
    }
}