import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { moveIn, fallIn } from '../shared/router.animation';
import { Observable } from 'rxjs';
import { DataSource } from '@angular/cdk/collections';
// import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { BackendService } from '../services/backend.service';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import { UserInfo} from '../model/userinfo'
import { FormBuilder, FormGroup, FormArray, FormControl, Validators } from '@angular/forms';
@Component({
    selector: 'app-teacher',
    templateUrl: './teacher.component.html',
    styleUrls: ['./teacher.component.css'],
    animations: [moveIn(), fallIn()],
    host: { '[@moveIn]': '' }
})
export class TeacherComponent implements OnInit, OnDestroy {
    //teacherRegistarionForm: FormGroup;
    members: any[];
    dataSource: MatTableDataSource<any>;
    myDocData;
    data$;
    toggleField: string;
    state: string = '';
    savedChanges = false;
    successMessage: String;
    error: boolean = false;
    errorMessage: String = "";
    dataLoading: boolean = false;
    private querySubscription;
    userinfo: UserInfo;
    addDataForm: FormGroup;
    editDataForm: FormGroup;

    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;
    displayedColumns = ['TEACHER_CODE', 'FIRST_NAME','MOBILE', '_id'];
    // file upload
    docId: string;
    fileName: string;
    showFileUpload: boolean = false;
    showDocument: boolean = false;
    docUrl: Observable<string | null>;
    classes: Array<String>;
    sections: Array<String>;
    genders: Array<String>;
    subjects: Array<String>;
    teacherCode:string;   
    teacherCount: Number;
    constructor(private backendService: BackendService,private fb: FormBuilder) { }

    ngOnInit() {
        this.toggleField = "searchMode";
        this.dataSource = new MatTableDataSource(this.members);
        this.initializeForm();
        //this.getEnrollmentCDs();
        this.fetchClasses();
        this.fetchSections();
        this.fetchGenders();
        this.fetchSubjects();
    }

    toggle(filter?) {
        if (!filter) { filter = "searchMode" }
        else { filter = filter; }
        this.toggleField = filter;
        this.dataLoading = false;
    }

    initializeForm(){
        this.addDataForm = this.fb.group({
            TEACHER_CODE: '',
            EMAIL: '',
            FIRST_NAME: ['', Validators.required],
            MIDDLE_NAME: '',
            LAST_NAME: ['', Validators.required],
            SEX: ['', Validators.required],
            DOB: ['', Validators.required],
            MOBILE: ['', [Validators.pattern("^[0-9]*$")]],
            line: this.fb.array([this.fb.group({
                CLASS_CODE: ['', Validators.required],
                SECTION_CODE: ['', Validators.required],
                SUBJECT_CODE: ['', Validators.required],
            })])
        });
        this.editDataForm = this.fb.group({
            TEACHER_CODE: '',
            EMAIL: '',
            FIRST_NAME: ['', Validators.required],
            MIDDLE_NAME: '',
            LAST_NAME: ['', Validators.required],
            SEX: ['', Validators.required],
            DOB: ['', Validators.required],
            MOBILE: ['', [Validators.pattern("^[0-9]*$")]],
            line: this.fb.array([]),
        });

    }

    LINES(formName) {
        return this[formName].get('line') as FormArray;
    }
    addLINES(formName) {
        this.LINES(formName).push(this.fb.group({
            CLASS_CODE: ['', Validators.required],
            SECTION_CODE: ['', Validators.required],
            SUBJECT_CODE: ['', Validators.required],
        }));
    }
    deleteLINES(index,formName) {
        this.LINES(formName).removeAt(index);
    }


    fetchClasses(){
        this.backendService.fetchDetails('Classes').subscribe(
          {
            next: details =>{
              this.classes=details;
              console.log(this.classes);
            }
          }
        );
    }
    
    fetchSections(){
        this.backendService.fetchDetails('Sections').subscribe(
          {
            next: details =>{
              this.sections=details;
              console.log(this.sections);
            }
          }
        );
    }

    fetchGenders(){
        this.backendService.fetchDetails('Genders').subscribe(
          {
            next: details =>{
              this.genders=details;
              console.log(this.genders);
            }
          }
        );
    }
    
    fetchSubjects(){
        this.backendService.fetchDetails('Subjects').subscribe(
          {
            next: details =>{
              this.subjects=details;
              console.log(this.subjects);
            }
          }
        );
    }

    setTeacherData(formData) {
        this.dataLoading = true;
        //Genereate the Teacher Code
        this.getTeacherCount(formData);
    }

    getTeacherCount(formData) {
        this.dataLoading = true;
        this.querySubscription = this.backendService.getDocs('TEACHER').subscribe((res) => {
            this.teacherCount = (res !=null) ? (res.length+1) : 1;
            console.log('this.teacherCount---->'+this.teacherCount);

            if(this.teacherCount < 10){
                this.teacherCode = 'TCH000'+this.teacherCount;
            }
            else if(this.teacherCount < 100){
                this.teacherCode = 'TCH00'+this.teacherCount;
            }
            else if(this.teacherCount < 1000){
                this.teacherCode = 'TCH0'+this.teacherCount;
            }
            else {
                this.teacherCode = 'TCH'+this.teacherCount;
            }
            console.log('this.teacherCode---->'+this.teacherCode);

            formData.TEACHER_CODE = this.teacherCode;
            formData.EMAIL = this.teacherCode+'@ig.com';
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
        },
        () => {
            this.dataLoading = false;
        });
        this.saveTeacherData(formData);
    }

    saveTeacherData(formData) {
        console.log('***********Inside saveTeacherData*****');
        this.backendService.setDoc('TEACHER',formData).then(
        (success) => {
            this.createuser(formData)
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
            this.savedChanges = false;
            }
        );
    }

    createuser(formData) {
        this.backendService.createUser(formData.EMAIL,'Test1234').then(
          (success) => {
            this.updateUserInfo(formData)
          },
          (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
            this.savedChanges = false;
          }
        );
    }
    
    updateUserInfo(formData) {
        this.userinfo = new UserInfo();
        this.userinfo.email = formData.EMAIL;
        this.userinfo.name =formData.FIRST_NAME +' '+formData.MIDDLE_NAME+' '+formData.LAST_NAME;
        this.userinfo.phone =formData.MOBILE;
        this.userinfo.role='Teacher';
        this.userinfo.secretKey='12345';
        this.userinfo.activated='Y';
        this.backendService.setDoc('USERS',this.userinfo).then(
            (success) => {
                this.savedChanges = true;
                this.successMessage ='Teacher Registered Successfull with Resgistration Code '+formData.STUDENT_CODE;
                this.error = false;
                this.errorMessage = "";
                this.dataLoading = false;
            },
            (error) => {
              this.error = true;
              this.errorMessage = error.message;
              this.dataLoading = false;
              this.savedChanges = false;
            }
        );
    }

    getData(formData?) {
        this.dataLoading = true;
        this.querySubscription = this.backendService.getDatalist('TEACHER','Student',formData).subscribe((res) => {
            this.dataSource = new MatTableDataSource(res);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
        },
        (error) => {
            this.error = true;
            this.errorMessage = error.message;
            this.dataLoading = false;
        },
        () => {
            this.dataLoading = false;
        });
    }

    updateData(formData) {
        this.dataLoading = true;
        this.querySubscription = this.backendService.updateDoc('TEACHER',formData._id,formData).then(res => {
            if (res) {
                this.savedChanges = true;
                this.error = false;
                this.errorMessage = "";
                this.dataLoading = false;
            }
        }
        ).catch(err => {
            if (err) {
                this.error = true;
                this.errorMessage = err.message;
                this.dataLoading = false;
            }
        }
        );
    }

    getDoc(docId) {
        this.docId = docId; // this is required to pass at file upload directive
        this.dataLoading = true;
        this.data$ = this.backendService.getDoc('TEACHER',docId);
        this.toggle('editMode');
        this.dataLoading = false;
    }
    getDocUrl(docUrl){
        this.fileName = docUrl;
        this.docUrl = this.backendService.getFileDownloadUrl(docUrl);
    }

    deleteDoc(docId) {
        if (confirm("Are you sure want to delete this record ?")) {
            this.dataLoading = true;
            this.backendService.deleteDoc('TEACHER',docId).then(res => {
                if (res) {
                    this.error = false;
                    this.errorMessage = "";
                    this.dataLoading = false;
                }
            }
            ).catch(err => {
                if (err) {
                    this.error = true;
                    this.errorMessage = err.message;
                    this.dataLoading = false;
                }
            }
            );
        }
    }

    //mat table paginator and filter functions
    ngAfterViewInit() {
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
    }

    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
        this.dataSource.filter = filterValue;
    }
    ngOnDestroy() {
        // this is not needed when observable is used, in this case, we are registering user on subscription
        if (this.querySubscription) {
            this.querySubscription.unsubscribe();
        }
    }
}