import { Injectable , NgZone  } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of , throwError} from 'rxjs';
import { switchMap, take } from 'rxjs/operators';
import { Router } from "@angular/router";
import { map, catchError } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { auth } from 'firebase/app';
import { firestore } from 'firebase/app';
import { User } from "../model/user";
@Injectable({
  providedIn: 'root'
})
export class BackendService {
  authState: any = null;
  userData: any; // Save logged in user data
  userInfo: any;
  constructor(
    public afAuth: AngularFireAuth, 
    private afsDB: AngularFirestore, 
    private httpClient: HttpClient, 
    private storage: AngularFireStorage,
    private ngZone: NgZone,
    private router: Router,) { 
    // this.afAuth.authState.subscribe(authState => {
    //   this.authState = authState;
    // })
    /* Saving user data in localstorage when
    logged in and setting up null when logged out */
    this.afAuth.authState.subscribe(user => {
      if (user) {
        this.authState = user;
        this.userData = user;
        localStorage.setItem('user', JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem('user'));
      } else {
        localStorage.setItem('user', null);
        JSON.parse(localStorage.getItem('user'));
      }
    })
  }

  getConfig() {
    return environment.social;
  }

  async createUser(email: any, password: any) {
    if (environment.database === 'firebase') {
      try {
        const result = await this.afAuth.createUserWithEmailAndPassword(email, password);
        /* Call the SendVerificaitonMail() function when new user sign
        up and returns promise */
        //this.SendVerificationMail();
        if(result.user !=null)
          this.SetUserData(result.user);
      }
      catch (error) {
        window.alert(error.message);
      }
    }
    if (environment.database === 'SQL') {
      // need to call SQL API here if a SQL Database is used
    }
  }

  // login page funcitons - login with FB/GOOGLE/EMAIL, if formData is passed, this means is user is using email/password login
  // Sign in with email/password
  async login(loginType, formData?) {
    if (formData) {
      //return this.afAuth.signInWithEmailAndPassword(formData.email, formData.password);
      try {
        const result = await this.afAuth.signInWithEmailAndPassword(formData.email, formData.password);
        console.log("result.user===>"+result.user.uid);
        this.SetUserData(result.user);
        localStorage.setItem("user",JSON.stringify(result.user));
        this.ngZone.run(() => {
          this.getUserInfo(formData.email)
          .subscribe(data =>{
            this.userInfo =data[0].payload.doc.data();
            console.log('this.userInfo role---->'+this.userInfo.role);
            localStorage.setItem("role",this.userInfo.role);
            localStorage.setItem("logedInName",this.userInfo.name);
            localStorage.setItem("logedInEmail",this.userInfo.email);
            console.log("role---->"+localStorage.getItem("role"));
            this.router.navigate(['/dashboard']);          
          });
        });
      }
      catch (error) {
        window.alert(error.message);
      }
  
    } else {
      let loginMethod;
      if (loginType === 'FB') { loginMethod = new auth.FacebookAuthProvider(); }
      if (loginType === 'GOOGLE') { loginMethod = new auth.GoogleAuthProvider(); }

      return this.afAuth.signInWithRedirect(loginMethod);
    }
  }

  getUserInfo(email: String) {
    //return this.getDoc('USERS', this.authState.uid);
    console.log('email--->'+email);
    return this.afsDB.collection(this.getCollUrls('USERS'),ref => ref.where('email', '==', email)).snapshotChanges();
    //return this.afsDB.collection(this.getCollUrls('USERS'), ref => ref.where('email', '==', email)).valueChanges();
  }

  // Returns true when user is looged in and email is verified
  get isLoggedIn(): boolean {
    const user = JSON.parse(localStorage.getItem('user'));
    return (user !== null && user.email !== null) ? true : false;
  }
  
  /* Setting up user data when sign in with username/password,
  sign up with username/password and sign in with social auth
  provider in Firestore database using AngularFirestore + AngularFirestoreDocument service */
  SetUserData(user) {
    const userRef: AngularFirestoreDocument<any> = this.afsDB.doc(`users/${user.uid}`);
    const userData: User = {
      uid: user.uid,
      email: user.email,
      displayName: user.displayName,
      photoURL: user.photoURL,
      emailVerified: user.emailVerified,
      password: 'Test1234'
    }
    return userRef.set(userData, {
      merge: true
    })
  }
  
  async logout() {
    localStorage.removeItem("role");
    localStorage.removeItem('user');
    await this.afAuth.signOut();
    this.router.navigate(['/login']);
  }

  // method to retreive firebase auth after login redirect
  redirectLogin() {
    return this.afAuth.getRedirectResult();
  }

  getUser(): Promise<any> {
    return this.afAuth.authState.pipe(take(1)).toPromise();
  }

  fetchDetails(searchKey : string) : Observable<String[]>{
    return this.httpClient.get('./assets/data/appdata.json').pipe(
      map(data =>{
        if(!!data[searchKey]){
          return data[searchKey];
        }
      }),
      catchError(this.handleError)     
    );
  }
  handleError(err: HttpErrorResponse){
    console.log(err);
    return throwError(err);
  }

  // function to send emails using a PHP API //
  sendEmail(messageData) {
    let httpOptions_e = {
      headers: new HttpHeaders({ 'Content-Type': 'application/X-www-form-urlencoded' })
    };
    return this.httpClient.post(environment.emailAPI, messageData, httpOptions_e);
  }

  getDatalist(coll: string, type:string, formData?) {
    if (formData) {
      if(type=='Student'){
        if (formData.STUDENT_CODE) {
          return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('STUDENT_CODE', '==', formData.STUDENT_CODE)).valueChanges();
        } 
        else {
          return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('FIRST_NAME', '==', formData.FIRST_NAME)).valueChanges();
        }
      }
      else if(type=='Teacher'){
        if (formData.TEACHER_CODE) {
          return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('TEACHER_CODE', '==', formData.TEACHER_CODE)).valueChanges();
        } 
        else {
          return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('FIRST_NAME', '==', formData.FIRST_NAME)).valueChanges();
        }      
      }        
    }
    else { // no search critera - fetch all docs
      return this.afsDB.collection(this.getCollUrls(coll)).valueChanges();
    } 
  }

  // setting page functions
  updateUser(formData): Promise<any> {
    // return this.setDoc('USERS', formData, this.afAuth.currentUser.uid);
    return this.setDoc('USERS', formData, this.authState.uid);
  }
  
  getUserDoc() {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid);
    return this.getDoc('USERS', this.authState.uid);
  }
  

  getUserStudentDoc() {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
  ));
  }
  getUserStudentMSGDoc() {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT')+'/'+res[0]['_id']+'/notifications').valueChanges()))));
   //.pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT')/res[0]['_id']/notifications, ref => ref.where('studentdocid', '==', res[0]['_id'])).valueChanges()))));
  }
  getUserStudentMSGCounts() {
    if(this.afAuth.currentUser != null) {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT')+'/'+res[0]['_id']+'/notifications', ref => ref.where('readReceipt', '==', true)).valueChanges()))));
   //.pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT')/res[0]['_id']/notifications, ref => ref.where('studentdocid', '==', res[0]['_id'])).valueChanges()))));
    } else return false;
  }
  getUserStudentFeeDoc() {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('FEE'), ref => ref.where('studentdocid', '==', res[0]['_id'])).valueChanges()))));
  }
  getUserStudentMarksDoc() {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('MARKS'), ref => ref.where('studentdocid', '==', res[0]['_id'])).valueChanges()))));
  }
  getUserStudentTutsDoc(_url) {
    // return this.getDoc('USERS', this.afAuth.auth.currentUser.uid)
    return this.getDoc('USERS', this.authState.uid)
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls('STUDENT'), ref => ref.where('SKEY', '==', res['phone'])).valueChanges()
   .pipe(switchMap(res => this.afsDB.collection(this.getCollUrls(_url), ref => ref.where('ENROLLMENT_CODE', '==', res[0]['ENROLLMENT_CODE'])).valueChanges()))));
  }

  // generic collection url pages and generic CRUD functions
  get timestamp() {
    const d = new Date();
    return d;
    // return firebase.firestore.FieldValue.serverTimestamp();
  }
  getCollUrls(coll) {
    let _coll = "USER_INFO";
    if (coll == "USERS") { _coll = "SMS_USERS"; }
    if (coll == "STUDENT") { _coll = "SMS_STUDENTS"; }
    if (coll == "TEACHER") { _coll = "SMS_TEACHERS"; }
    if (coll == "ENROLL_CD") { _coll = "SMS_CONFIG_ENROLL_CD"; }
    if (coll == "FEE_CD") { _coll = "SMS_CONFIG_FEE_CD"; }
    if (coll == "MARKS_CD") { _coll = "SMS_CONFIG_MARKS_CD"; }
    if (coll == "FEE") { _coll = "SMS_FEE"; }
    if (coll == "MARKS") { _coll = "SMS_MARKS"; }
    if (coll == "EMPLOYEE") { _coll = "SMS_EMPLOYEE"; }
    if (coll == "SALARY_CD") { _coll = "SMS_SALARY_CD"; }
    if (coll == "SALARY") { _coll = "SMS_SALARY"; }
    if (coll == "VOUCHER") { _coll = "SMS_VOUCHER"; }
    if (coll == "EXPENSES") { _coll = "SMS_EXPENSES"; }
    if (coll == "ASSIGNMENT") { _coll = "SMS_ASSIGNMENT"; }
    if (coll == "CLASSES") { _coll = "SMS_CLASSES"; }
    if (coll == "HOMEWORK") { _coll = "SMS_HOMEWORK"; }
    if (coll == "TUTORIALS") { _coll = "SMS_TUTORIALS"; }
    return _coll;
  }
  setDoc(coll: string, data: any, docId?: any) {
    const id = this.afsDB.createId();
    const item = { id, name };
    if (docId) { item.id = docId; }
    const timestamp = this.timestamp
    var docRef = this.afsDB.collection(this.getCollUrls(coll)).doc(item.id);
    return docRef.set({
      ...data,
      _id: id,
      updatedAt: timestamp,
      createdAt: timestamp,
      // username: this.afAuth.auth.currentUser.displayName,
      // useremail: this.afAuth.auth.currentUser.email,
      // author: this.afAuth.auth.currentUser.uid
      // username: this.authState.currentUser.displayName,
      username: this.authState.displayName,
      useremail: this.authState.email,
      author: this.authState.uid
    }).then((res) => { return true });
  }
  updateDoc(coll: string, docId: string, data: any) {
    const timestamp = this.timestamp
    var docRef = this.afsDB.collection(this.getCollUrls(coll)).doc(docId);
    return docRef.update({
      ...data,
      updatedAt: timestamp,
      // username: this.afAuth.auth.currentUser.displayName,
      // useremail: this.afAuth.auth.currentUser.email,
      // author: this.afAuth.auth.currentUser.uid
      username: this.authState.displayName,
      useremail: this.authState.email,
      author: this.authState.uid
    }).then((res) => { return true });
  }
  updateFileUpload(coll: string, docId: string, filePath: string) {
    const timestamp = this.timestamp;
    const docRef = this.afsDB.collection(this.getCollUrls(coll)).doc(docId);
    return docRef.update({
      files: firestore.FieldValue.arrayUnion(filePath),
      updatedAt: timestamp,
      // username: this.afAuth.auth.currentUser.displayName,
      // useremail: this.afAuth.auth.currentUser.email,
      // author: this.afAuth.auth.currentUser.uid
      username: this.authState.displayName,
      useremail: this.authState.email,
      author: this.authState.uid
    });
  }
  getFileDownloadUrl(url) {
    const ref = this.storage.ref(url);
    return ref.getDownloadURL();
  }
  deleteDoc(coll: string, docId: string) {
    const timestamp = this.timestamp
    var docRef = this.afsDB.collection(this.getCollUrls(coll)).doc(docId);
    return docRef.delete().then((res) => { return true });
  }
  getDoc(coll: string, docId: string) {
    return this.afsDB.collection(this.getCollUrls(coll)).doc(docId).valueChanges();
  }
  
  getDocs(coll: string, formData?) {
    if (formData) {
      if (formData.code) {
        return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('code', '>=', formData.code)).valueChanges();
      } else {
        return this.afsDB.collection(this.getCollUrls(coll), ref => ref.where('descr', '>=', formData.descr)).valueChanges();
      }
    } else { // no search critera - fetch all docs
      return this.afsDB.collection(this.getCollUrls(coll)).valueChanges();
    }
  }
}
